import './CSS/General.css';
import { Switch, NavLink, Route } from "react-router-dom";
import { AnimatePresence, motion } from "framer-motion";
import { useLocation, useHistory } from 'react-router-dom/cjs/react-router-dom';
import { useState } from 'react';
import getResults from "./JS/days_Calculator_Results";

export default function App() {

  var location = useLocation();

  var buttonOfCalcOfDaysStyle = { width: "12vw" };

  if (window.innerWidth < 630) {
    buttonOfCalcOfDaysStyle.width = "52vw";
  }

  return (
    <div className="containerMain">
      <div id="main" className="main">
        <nav className="allPages">
          <ul>
            <li>
              <NavLink to="/" exact>
                Home
              </NavLink>
            </li>
            <li>
              <NavLink to="/Credits">
                Credits
              </NavLink>
            </li>
            <li className="buttonOfCalcOfDays">
              <NavLink to="/Days_Calculator" style={buttonOfCalcOfDaysStyle}>
                Days Calculator
              </NavLink>
            </li>
          </ul>
        </nav>
        <AnimatePresence exitBeforeEnter>
          <Switch location={location} key={location.pathname}>
            <Route exact path="/" component={IndexPage} />
            <Route path="/Credits" component={CreditsPage} />
            <Route path="/section_1" component={Section_1} />
            <Route path="/Days_Calculator" component={Days_Calculator} />
          </Switch>
        </AnimatePresence>
      </div>
    </div>
  );

}


const pageVariants = {
  initial: {
    opacity: 0,
    y: "-5vh"
  },
  in: {
    opacity: 1,
    y: 0
  },
  out: {
    opacity: 0
  },
  initialPage: {
    opacity: 0,
    x: "-86vw",
    y: "12vh"
  },
  inPage: {
    opacity: 1,
    x: "-31vw",
    y: "12vh"
  },
  outPage: {
    opacity: 0
  }
};

const homeTransition = {
  type: "tween",
  ease: "anticipate",
  duration: 0.5
};

const pageTransition = {
  type: "tween",
  ease: "anticipate",
  duration: 0.8
};

function IndexPage() {

  var history = useHistory();

  return (
    <motion.div
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={homeTransition}
    >
      <div className="title">
        <h2>Welcome to</h2> <h1>The Don't Starve Long guide</h1>
      </div>
      <div className="description">
        <h3>This is a fan-based site made to collect all sort of tips, tricks and guides in one organised site. </h3>
        <button className="startNowButton" onClick={() => { history.push("/section_1") }}>
          Start Now
        </button>
      </div>
    </motion.div>
  )
}

function CreditsPage() {
  return (
    <motion.div
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={homeTransition}
    >
      <div className="description">
        <h3>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Cupiditate laborum ratione aspernatur, quas nostrum perferendis est autem quod!</h3>
      </div>
    </motion.div>
  )
}


var pageName = "Home";

function Section_1() {

  const sectionTransition = {
    type: "tween",
    ease: "anticipate",
    duration: 1.2
  };

  var SectionStyle = {
    position: "relative",
    overflow: "hidden",
    left: "6.5vw",
    width: "69vw",
    height: "82vh"
  }

  if (window.innerWidth < 630) {
    pageVariants.inPage.x = "-34vw";
    SectionStyle.width = "77vw";
  }

  return (
    <div>
      <motion.div
        initial="initial"
        animate="in"
        exit="out"
        variants={pageVariants}
        transition={sectionTransition}
      >
        <div className="titleSection">
          <h1>Section 1, Page {pageName}</h1>
        </div>
      </motion.div>
      <div style={SectionStyle}>
        <motion.div
          initial="initialPage"
          animate="inPage"
          exit="outPage"
          variants={pageVariants}
          transition={pageTransition}
          className="section"
        >
          <h2>INDEX:</h2>
          <h3>Introduction</h3>
          <h3>Navigation</h3>
          <h3>How to Play</h3>
          <h3>Basic Tips</h3>
        </motion.div>
      </div>
    </div>
  )
}

function Days_Calculator() {

  const [results, createResults] = useState(<h3 className="dataText">The data'll go here.</h3>);

  const inputsStyle = {
    position: "relative",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    top: "7vh"
  };

  const getData_Enter = (e) => {
    if (e.keyCode === 13) {
      getData()
    }
  }

  function getData() {
    var days = document.getElementById("daysPassed_SearchBar").value;
    createResults(getResults(days));
  }

  var descriptionStyle = {};
  var daysPassed_DescriptionStyle = { marginBottom: "10px", textAlign: "center" };

  if (window.innerWidth > 630) {
    descriptionStyle.height = "49vh";
  } else {
    descriptionStyle.height = "47vh";
    daysPassed_DescriptionStyle.fontSize = "5.7vw";
  }


  return (
    <motion.div
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={homeTransition}
    >
      <div className="title">
        <h1 className="daysPassed_Title">Days Calculator</h1>
        <h2 className="daysPassed_Description" style={daysPassed_DescriptionStyle}>An useful calculator that calculates the days remaining before a certain moon cycle and season</h2>
      </div>
      <div className="description" style={descriptionStyle}>
        <div style={inputsStyle}>
          <input type="search" id="daysPassed_SearchBar" placeholder="Insert days passed" onKeyDown={(e) => { getData_Enter(e); }} />
          <button className="daysPassed_Button" onClick={() => { getData() }}>
            <i className="fas fa-calculator" />
          </button>
        </div>
        {results}
      </div>
    </motion.div>
  )
}